<template>
  <div class="hot">
    <div v-if="listInfo" class="top">
      <div class="time">
        <div class="t1"></div>
        更新时间: {{this.listInfo.trackUpdateTime|formatDate('m 月 d 日')}}
      </div>
    </div>
    <ul class="kong">
      <img src="../assets/all.png" alt="">
      <!-- 循环热歌榜列表 -->
      <li-item v-for="(item,index) in hotList" :item="item" :key="item.id" @play="$emit('play',$event)" :playMusic="playMusic">
        
        <template v-slot:sortnum>
            <b>{{((index+1) &lt; 10) ? '0'+(index+1):index+1}}</b>
        </template>
      </li-item>
      <loading v-if="isLoading"></loading>

    </ul>

    <!-- <p @click="loadeMore" class="xiala">下拉可以获取更多歌曲</p> -->
  </div>
</template>

<script>
// @ is an alias to /src
import LiItem from '@/components/LiItem'
import Loading from '@/components/Loading'
export default {
  name: 'Hot',
  props:["playMusic"],
  data(){
    return {
      listInfo:null,
      hotList:[],
      allList:[],
      isLoading:true,
      page:0,//记录页码
      perpage:20//每页显示20条
    }
  },
  components: {
    LiItem,Loading
  },
  mounted(){
    window.addEventListener('scroll',this.debounce(this.handleScroll,1000))
  },
  beforeRountLeave(to,from,next){
    window.removeEventListener('scroll',this.debounce(this.handleScroll,1000));
    next();
  },
  methods:{
    debounce(fn, wait) {
      let timeout = null
      return function() {
        if(timeout !== null) clearTimeout(timeout)     
        timeout = setTimeout(fn, wait);
      }
    },
    handleScroll(){
      // console.log(document.documentElement.scrollTop);
      // console.log(document.documentElement.scrollHeight);
      // console.log(document.documentElement.clientHeight);
      let scrollA = document.documentElement.scrollHeight;
      let scrollC = document.documentElement.scrollTop;
      let scrollB = document.documentElement.clientHeight;
      let whnb = scrollA-(scrollC+scrollB);
      // console.log(whnb);
      if(whnb<3){
        // console.log('nb');
          this.page++; // 1  2
          let start=(this.page)*this.perpage;// 20   40
          let end=(this.page+1)*this.perpage;// 40   60
          let zhang = this.allList.slice(start,end);
          this.hotList=this.hotList.concat(zhang);
          
        
      }
    }
  },
  created(){
    this.axios.get('/playlist/detail?id=3778678').then(d=>{
      // console.log(d);
      this.listInfo=d.data.playlist;
      this.allList=d.data.playlist.tracks.map((data,index)=>{
        data.song={
          privilege:d.data.privileges[index],
          artists:data.ar,
          album:data.al
        }
        return data;
      });
      // slice(0,20)=>1 slice(20,40)=>2 slice(40,60)=>3
      //每页显示 20
      this.hotList=this.allList.slice(0,20);
    });
  },
  filters:{
    formatDate(value,fstr){
      let d=new Date(value);
      let formatO={
        'Y+':d.getFullYear(),
        'm+':d.getMonth()+1,
        'd+':d.getDate(),
        'h+':d.getHours(),
        'M+':d.getMinutes(),
        'S+':d.getSeconds
      }
      for(let k in formatO){
        let reg=new RegExp(k);
        let val=formatO[k];
          val=val<10?'0'+val:val;
        fstr=fstr.replace(reg,val);
      }
      return fstr;
    }
  }
}
</script>
